@font-face {
  font-family: "Manrope";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/manrope.woff2") format("woff2"), url("../fonts/manrope.woff") format("woff");
}
@font-face {
  font-family: "Manrope SemiBold";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/manrope-semibold.woff2") format("woff2"), url("../fonts/manrope-semibold.woff") format("woff");
}
@font-face {
  font-family: "Manrope Bold";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/manrope-bold.woff2") format("woff2"), url("../fonts/manrope-bold.woff") format("woff");
}
@font-face {
  font-family: "Manrope ExtraBold";
  font-weight: 800;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/manrope-extrabold.woff2") format("woff2"), url("../fonts/manrope-extrabold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-width: 16.6666666667vw;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 0.625vw;
  line-height: 1.6666666667rem;
  font-family: "Manrope", "Arial", sans-serif;
  color: #2a2a2a;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
@media (max-width: 992px) {
  html {
    font-size: 12px;
  }
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #2a2a2a;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 100vw;
  margin: 0 auto;
  padding: 0 7.1354166667vw;
}
@media (max-width: 992px) {
  .container {
    padding: 0 1.8333333333rem;
  }
}

.btn {
  display: inline-block;
  padding: 1.3020833333vw 4.6875vw 1.3020833333vw 2.8125vw;
  font-family: "Manrope ExtraBold", "Arial", sans-serif;
  font-weight: 600;
  font-size: 0.8333333333vw;
  line-height: 1.25vw;
  letter-spacing: 0.04em;
  color: #ffffff;
  background-color: transparent;
  border: none;
  text-align: center;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (max-width: 992px) {
  .btn {
    font-size: 0.8333333333rem;
    line-height: 1.25rem;
  }
}

.btn--advantages {
  position: relative;
  width: 100%;
  background-color: #fd2016;
  border-radius: 26.0416666667vw;
  overflow-x: hidden;
}
.btn--advantages::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.6666666667vw;
  width: 1.1458333333vw;
  height: 1.1458333333vw;
  transform: translate(-50%, -50%);
  background-image: url("../img/svg/arrow-button-calculation.svg");
  background-repeat: no-repeat;
  transition: 0.3s;
}
.btn--advantages::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 2.34375vw;
  height: 100%;
  transform: skewX(-45deg);
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.4));
  animation: flareAnimation 3s linear infinite;
}
@media (max-width: 992px) {
  .btn--advantages {
    min-height: 5rem;
    padding: 1.3333333333rem 3.5833333333rem;
  }
  .btn--advantages::before {
    content: none;
  }
}

@keyframes flareAnimation {
  0% {
    left: -150%;
  }
  100% {
    left: 150%;
  }
}
.btn--advantages-whatsapp {
  padding: 1.8229166667vw 1.71875vw 1.71875vw 3.125vw;
  background-color: rgba(0, 0, 0, 0.45);
  border: 1px solid #ffffff;
}
.btn--advantages-whatsapp::before {
  left: 2.1875vw;
  width: 1.5625vw;
  height: 1.5625vw;
  background-image: url("../img/svg/icon-whatsapp.svg");
}
@media (max-width: 992px) {
  .btn--advantages-whatsapp {
    min-height: 5rem;
    padding: 1.6666666667rem 4rem;
  }
}

@media (max-width: 992px) {
  .btn--advantages-whatsapp span {
    color: #20c863;
  }
}

.btn--advantages:hover {
  opacity: 0.7;
}
.btn--advantages:hover::before {
  top: 53%;
  rotate: 45deg;
}

.btn--advantages:active {
  opacity: 0.4;
}

.btn--advantages-whatsapp:hover::before {
  top: 50%;
  rotate: none;
}

.btn--form-send {
  display: block;
  width: 100%;
  min-height: 4.4270833333vw;
  padding: 0.78125vw 0.78125vw 0.7291666667vw;
  font-family: "Manrope SemiBold", "Arial", sans-serif;
  font-weight: 600;
  font-size: 0.8333333333vw;
  line-height: 1.25vw;
  background-color: #fd2016;
  border-radius: 26.0416666667vw;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(42, 42, 42, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 3.125vw 2.0833333333vw;
}
@media (max-width: 992px) {
  .modal__wrapper {
    padding: 5rem 1.3333333333rem;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 26.0416666667vw;
  padding: 2.34375vw 3.5416666667vw 3.90625vw;
  background-image: url("../img/background-form.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 0;
  border-radius: 0.15625vw;
}
@media (min-width: 993px) and (min-resolution: 1.5dppx) {
  .modal__content {
    background-image: url("../img/background-form@2x.webp");
  }
}
.modal__description {
  margin: 0;
  font-family: "Manrope", "Arial", sans-serif;
  font-size: 1.5625vw;
  line-height: 2.1875vw;
  text-align: center;
  color: #ffffff;
}
.modal__text {
  margin: 0;
  font-size: 0.9375vw;
  line-height: 1.4583333333vw;
  text-align: center;
  color: #ffffff;
}
.modal__close-btn {
  position: absolute;
  top: 0.9375vw;
  right: 0.8333333333vw;
  z-index: 1;
  width: 2.0833333333vw;
  height: 2.0833333333vw;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 992px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.advantages {
  position: relative;
  min-height: 47.4479166667vw;
  background-color: #2a2a2a;
  background-image: url("../img/background-desktop.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 0;
}
@media (min-width: 993px) and (min-resolution: 1.5dppx) {
  .advantages {
    background-image: url("../img/background-desktop@2x.webp");
  }
}
@media (max-width: 992px) {
  .advantages {
    min-height: 42.5833333333rem;
    height: 42.5833333333rem;
    background-image: url("../img/background-mobile.webp");
  }
}
@media (max-width: 992px) and (min-resolution: 144dpi), (max-width: 992px) and (min-resolution: 1.5dppx) {
  .advantages {
    background-image: url("../img/background-mobile@2x.webp");
  }
}
.advantages:before {
  position: absolute;
  content: "";
  top: 50%;
  left: 3.3854166667vw;
  width: 0.0520833333vw;
  height: 25.4166666667vw;
  transform: translate(-50%, -50%);
  background-color: #efefef;
}
@media (max-width: 992px) {
  .advantages:before {
    content: none;
  }
}

.advantages__wrap,
.advantages__inner {
  display: block;
  max-width: 79.375vw;
  min-width: 37.5vw;
  margin: 0 auto;
  padding: 6.6666666667vw 0 6.4583333333vw 3.125vw;
}
@media (max-width: 992px) {
  .advantages__wrap,
.advantages__inner {
    max-width: 79rem;
    min-width: 23rem;
    padding: 7.9166666667rem 1.8333333333rem 3.3333333333rem;
  }
}

.advantages__wrap h1 {
  width: 52.65625vw;
  margin: 0;
  font-weight: 400;
  font-size: 3.3854166667vw;
  line-height: 4.9479166667vw;
  letter-spacing: 0.02em;
  color: #ffffff;
}
@media (max-width: 992px) {
  .advantages__wrap h1 {
    width: 23rem;
    font-size: 1.6666666667rem;
    line-height: 2.4166666667rem;
  }
}

.advantages__title {
  font-family: "Manrope Bold", "Arial", sans-serif;
  font-weight: 700;
}

.advantages__title-text {
  display: inline-block;
  padding: 2.34375vw 0;
  font-size: 1.40625vw;
  line-height: 2.34375vw;
  letter-spacing: 0.02em;
  color: #ffffff;
}
@media (max-width: 992px) {
  .advantages__title-text {
    padding: 2.3333333333rem 0;
    font-size: 1rem;
    line-height: 1.5rem;
  }
}

.advantages__button {
  display: grid;
  grid-template-columns: repeat(2, 20.8333333333vw);
  min-height: 5.2083333333vw;
  gap: 1.1458333333vw;
}
@media (max-width: 992px) {
  .advantages__button {
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
    min-width: 23rem;
    min-height: 5rem;
  }
}

.advantages__button-inner {
  display: flex;
}

.advantages__list {
  display: grid;
  grid-template-columns: 10.7291666667vw 11.5625vw 7.3958333333vw 20vw;
  gap: 9.8958333333vw;
  min-height: 7.96875vw;
  margin: 0;
  padding: 0;
  font-size: 0.8854166667vw;
  line-height: 1.40625vw;
  letter-spacing: 0.02em;
  list-style-type: none;
  color: #ffffff;
}
@media (max-width: 992px) {
  .advantages__list {
    grid-template-columns: minmax(11.9166666667rem, auto) minmax(10rem, auto);
    gap: 1.4166666667rem 1.25rem;
    font-size: 0.8333333333rem;
    line-height: 1.25rem;
  }
}

.advantages__item {
  position: relative;
  padding-top: 1.6666666667vw;
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0.6770833333vw;
  height: 0.5208333333vw;
  background-image: url("../img/svg/check-mark.svg");
  background-repeat: no-repeat;
}
.advantages__item::after {
  content: "";
  position: absolute;
  top: 0;
  right: -4.9479166667vw;
  width: 0.0520833333vw;
  height: 100%;
  background-color: #ffffff;
  opacity: 0.25;
}
.advantages__item:last-child::after {
  content: none;
}
@media (max-width: 992px) {
  .advantages__item {
    padding-top: 1.3333333333rem;
    color: #282828;
  }
  .advantages__item::after {
    content: none;
  }
}

.advantages__item-text {
  margin: 0;
  font-family: "Manrope ExtraBold", "Arial", sans-serif;
  font-weight: 700;
}

.advantages__inner {
  padding: 0 3.125vw;
}
@media (max-width: 992px) {
  .advantages__inner {
    padding: 1.3020833333vw 1.1458333333vw 0;
  }
  .advantages__inner::after {
    content: none;
  }
}

.advantages__scroll-down {
  position: absolute;
  bottom: 15%;
  left: 3.3854166667vw;
  transform: translate(-50%, -50%);
}
@media (max-width: 992px) {
  .advantages__scroll-down {
    display: none;
  }
}

.advantages__scroll-down-inner {
  position: relative;
}
.advantages__scroll-down-inner:before {
  content: "";
  position: absolute;
  top: 30%;
  left: 50%;
  width: 0.2083333333vw;
  height: 0.2083333333vw;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  border: solid 0.0520833333vw #fd2016;
  animation: mouse-wheel 0.8s linear infinite;
}

@keyframes mouse-wheel {
  0% {
    top: 0.5729166667vw;
  }
  25% {
    top: 0.6770833333vw;
  }
  50% {
    top: 0.78125vw;
  }
  75% {
    top: 0.6770833333vw;
  }
  100% {
    top: 0.5729166667vw;
  }
}
@media (max-width: 992px) {
  .advantages__item--mobile {
    order: 4;
  }
}

.form__text {
  padding-left: 35px;
  font-size: 0.7291666667vw;
  line-height: 0.9895833333vw;
  color: #ffffff;
}

.form__field {
  padding-top: 30px;
}

.form__field input {
  width: 100%;
  min-height: 4.4270833333vw;
  border-radius: 3.8020833333vw;
  background-color: #fafafa;
  box-shadow: inset 0 0 1.09375vw rgba(255, 255, 255, 0.4);
}
.form__field input::placeholder {
